<template>
    <div>
        <hand></hand>


        <template v-if="isfo">
            <che3></che3>

            <div id="zhuti">
                <input type="text" id="in2"  placeholder="  ..." v-model="namelikes" value="" /><br>
                <input type="submit" @click="like" id="an" value=""  />
                <input type="submit" @click="loadPosts" id="an2" value=""  />
<!--                <p>{{page.total}}</p>-->
                <div id="wr">
                    <span id="span"></span>
                    <hr style="color: gray;">
                    <span id="shu">{{page.total}}</span>
                </div>

                <el-pagination
                        background
                        layout="prev, pager, next"
                        @current-change="handlePageChange"
                        :current-page="page.current"
                        :page-size="page.size"
                        :total="page.total" id="tiao"
                        style="margin-bottom: 20px;margin-left: 50px">
                </el-pagination><!---->

                <span id="yhid">id</span>
                <span id="tys"></span>
                <span id="lx"></span>
                <span id="sj"></span>

                <div v-for="yh in yonhu" :key="yh.id">

                    <template v-if="yh.viptime1 == 999">
                        <div class="yyzhuti2">
                            <div class="fj"></div>
                            <img :src=" '/api/img2/'+yh.tp " class="tp">
                            <div class="yid">{{yh.id}}</div>
                            <div class="name">{{yh.name}}</div>
                            <!--                        <span class="hui" v-if="yy.mvip!=1"></span>-->
                            <!--                        <span class="hui2" v-else></span>-->
                            <div class="js" v-if="yh.vip!=1"></div>
                            <div class="js2" v-else></div>
                            <div class="time">{{yh.time}}</div>
                            <!--                        <a href="#" class="sc"  @click.prevent="deleteComment(yh.id)"></a>-->
                            <input type="submit" @click="deleteComment(yh.id)" class="ana" value=""  />
                            <input type="submit" @click="jiefen(yh.id)" class="ana2" value=""  />
                        </div>
                    </template>

                    <template v-else>
                        <div class="yyzhuti">
                            <img :src=" '/api/img2/'+yh.tp " class="tp">
                            <div class="yid">{{yh.id}}</div>
                            <div class="name">{{yh.name}}</div>
                            <!--                        <span class="hui" v-if="yy.mvip!=1"></span>-->
                            <!--                        <span class="hui2" v-else></span>-->
                            <div class="js" v-if="yh.vip!=1"></div>
                            <div class="js2" v-else></div>
                            <div class="time">{{yh.time}}</div>
                            <!--                        <a href="#" class="sc"  @click.prevent="deleteComment(yh.id)"></a>-->
                            <input type="submit" @click="deleteComment(yh.id)" class="ana" value=""  />
                        </div>
                    </template>

                </div>
            </div>

        </template>


        <template v-else>
            <h1>...</h1>
        </template>
    </div>
</template>

<script>
    import hand from '../components/hand';
    import che3 from '../components/che3';

    export default {
        name: "kan4",
        components: {
            hand , che3
        },
        data() {
            return {
                isfo: false,
                page: {
                    current: 1,//
                    size: 7,   //
                    total: 0    //
                },
                yonhu: [],
                namelikes: ''
            }
        },
        methods: {
            doLogin() {
                this.$axios({
                    url: "/api/pd",
                }).then(resp =>{
                    console.log(resp);

                    if (resp.data.data.gname == null){
                        this.isfo = false
                    }else {
                        this.isfo = true
                    }
                });
            },
            loadPosts() {
                this.$axios({
                    url: "/api/kanyh",
                    params: {
                        curr: this.page.current,
                        size: this.page.size
                    }
                }).then(r => {
                    console.log(r);
                    this.yonhu = r.data.data;
                    this.page.total = r.data.page.total;
                }).catch(e => {
                    alert(e);
                });
            },
            handlePageChange(v) {
                this.page.current = v;
                this.loadPosts();
            },
            deleteComment(id) {
                this.$confirm("?")
                    .then(() => {
                        this.$axios({
                            url: '/api/scyh?id=' + id
                        }).then(r => {
                            this.$message("!");
                            this.loadPosts();
                        }).catch(e => {
                            this.$alert(e);
                        });
                    });
            },
            jiefen(id) {
                this.$axios({
                    url: "/api/jiechu?id=" + id,
                }).then(resp =>{
                    this.$message("!");
                    this.loadPosts();
                });
            },
            like() {
                var rsp = new URLSearchParams();
                rsp.append("name", this.namelikes);
                this.$axios({
                    url: "/api/czyh",
                    method: "post",
                    data: rsp,
                }).then(resp =>{
                    this.yonhu = resp.data.data;
                });
            }

        },
        created() {
            this.doLogin();
            this.loadPosts();
        }
    }
</script>

